import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {DatePipe} from '@angular/common';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';

import {CollectionsService} from '../../../../../core/services/collections.service';
import {ItemService} from '../../../../../core/services/item.service';
import {ClientService} from '../../../../../core/services/client.service';
import {TaxService} from '../../../../../core/services/tax.service';
import {AuthenticationService} from '../../../../../core/services/authentication.service';
import {NgOption} from '@ng-select/ng-select';
import {environment} from '../../../../../../environments/environment';
import { SeoService } from 'src/app/core/services/seo.service';
import * as Dropzone from 'dropzone';

@Component({
    selector: 'app-collection-edit',
    templateUrl: './collection-edit.component.html',
    styleUrls: ['./collection-edit.component.scss']
})

export class CollectionEditComponent implements OnInit {
    editCollectionForm: FormGroup;
    selectedItem: number;
    loginUser: any;
    urlPage:string;
    collection: any;
    seoData: any;
    moduletype: string;
    listCondition: NgOption[] = [
        {value: 0, label: '>'},
        {value: 1, label: '<'},
        {value: 2, label: '='}
    ];
    listFilterType: NgOption[] = [
        {value: 0, label: 'Select'},
        {value: 1, label: 'Auto'},
    ];
    listFilterProductNeed: NgOption[] = [
        {value: 0, label: 'All Conditions'},
        {value: 1, label: 'Any condition'},
    ];

    listFilterCondition: NgOption[] = [
        {type: 0, condition: null, value: null, name: "Price"},
        {type: 1, condition: null, value: null, name: "Compare Price"},
        // {type: 2, condition: null, value: null, name: "Tag"}
    ];
    isLogoLoaded = true;
    listcollections = [];
    listTemplate: any = [];
    isFormSubmitted = false;
    isFormLoaded = false;
    liststatus: NgOption[] = [
        {value: 'yes', label: 'Show'},
        {value: 'no', label: 'Hide'},
    ];
    datepickerConfig = {
        dateInputFormat: 'YYYY-MM-DD',
        containerClass: 'theme-red'
    };
    isCollapsedHead1 = false;
    isCollapsedHead2 = false;
    isCollapsedHead3 = false;
    @ViewChild('logodropzone', {static: false}) logodropzone: ElementRef;
    private apiUrl = environment.apiUrl;
    constructor(
        public translate: TranslateService,
        public datepipe: DatePipe,
        private router: Router,
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private collectionService: CollectionsService,
        private clientService: ClientService,
        private itemService: ItemService,
        private taxService: TaxService,
        private seoService: SeoService,
        private authenticationService: AuthenticationService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
        this.route.paramMap.subscribe(params => {
            this.getById(params.get('id'));
        });
    }

    get collectionControl() {
        return this.editCollectionForm.controls;
    }

    ngOnInit() {
        if (this.router.url.indexOf('/productcollections/edit') !== -1) {
            this.moduletype = 'products';
            this.urlPage = '/productcollections';
        } else {
            this.moduletype = 'news';
            this.urlPage = '/newscollections';
        }
    }

    loadForm() {
        let that = this;
        this.editCollectionForm = this.formBuilder.group({
            id: [this.collection.id, Validators.required],
            name: [this.collection.name, Validators.required],
            description: [this.collection.description],
            page_title: [this.collection.page_title],
            page_description: [this.collection.page_description],
            url_and_handle: [this.collection.url_and_handle, Validators.required],
            active: [this.collection.active, Validators.required],
            avatar: [this.collection.avatar, Validators.required],
            keyword: [this.collection.keyword],
            seoData: this.formBuilder.array([]),
            seo: [this.collection.seo],
            condition_type: [this.collection.condition_type],
            type: [this.collection.type],
            conditions: [this.collection.conditions],
            filter: this.formBuilder.array([]),
            po_types_indentify:[this.moduletype],
            pages_id: [this.collection.pages_id, Validators.required],
        });
        this.editCollectionForm.patchValue({avatar: this.collection.avatar});
        setTimeout(() => {
            that.loadDropzone();
        });
        for (let seo of this.collection.seoData) {
            this.onAddMetaTag(seo);
        }
        for (let filter of this.collection.filter) {
            this.addFilterCondition(filter);
        }
        this.isFormLoaded = true;
    }

     private getFilterCondition(data) {
        return this.formBuilder.group({
            type: [data.type, Validators.required],
            name: [data.name],
            condition: [data.condition, Validators.required],
            value: [data.value, Validators.required]
        });
    }
    get controllFilter(): FormArray {
        return this.editCollectionForm.get('filter') as FormArray;
    } 
    private addFilterCondition(data) {
        const controlFilter = <FormArray>this.editCollectionForm.controls['filter'];
        controlFilter.push(this.getFilterCondition(data));
    }

    onAddFilterCondition(even: any) {
        const control = <FormArray>this.editCollectionForm.controls['filter'];
        control.push(this.getFilterCondition(even));
    }
    onRemoveFilterCondition(even: any) {
        for (var i = 0; i < this.editCollectionForm.value.filter.length; ++i) {
            if(even.value.type == this.editCollectionForm.value.filter[i].type){
                (<FormArray>this.editCollectionForm.get('filter')).removeAt(i)
            }
        }
    }


    getAllMeta() {
        this.seoService.getAllMeta()
            .subscribe(
                data => {
                    this.seoData = data;
                    this.loadForm();
                });
    }
    private getSeoData(data) {
        !data.content ? data.content = null : '';
        return this.formBuilder.group({
            meta: [data.label, Validators.required],
            meta_id: [data.id, Validators.required],
            content: [data.content]
        });
    }
    private addSeoData(data) {
        const control = <FormArray>this.editCollectionForm.controls['seoData'];
        control.push(this.getSeoData(data));
    }
    onAddMetaTag(even: any) {
        const control = <FormArray>this.editCollectionForm.controls['seoData'];
        control.push(this.getSeoData(even));
    }
    onRemoveMetaTag(even: any) {
        (<FormArray>this.editCollectionForm.get('seoData')).removeAt(even.index)
    }
    getById(collectionId) {
        this.collectionService.getById(collectionId).subscribe(data => {
            this.collection = data;
            this.getTemplate();
            this.getCollections();
        });
    }
    getCollections() {
        this.collectionService.getAll().subscribe(
            data => {
                this.listcollections = data;

                this.getAllMeta();
            });
    }

    getTemplate() {
        this.collectionService.getTemplate().subscribe(
            data => {
                this.listTemplate = data;
            });
    }
    onSubmit() {
        this.isFormSubmitted = true;
        if (this.editCollectionForm.invalid) {
            return;
        }

        this.collectionService.update(this.editCollectionForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('collections.messages.update'), this.translate.instant('collections.title'));
                    this.router.navigate([this.urlPage]);
                });
    }
    loadDropzone() {
        let that = this;
        this['fileDropZone'] = new Dropzone(this.logodropzone.nativeElement, {
            url: 'https://httpbin.org/post',
            maxFiles: 1,
            clickable: true,
            acceptedFiles: 'image/*',
            createImageThumbnails: true,
            init: function () {
                const _this = this;
                that.editCollectionForm.get('avatar').value ? _this.previewsContainer.lastChild['hidden'] = true : _this.previewsContainer.lastChild['hidden']= false;
                this.on('addedfile', function (file) {
                    const removeButton = Dropzone.createElement('<button class=\'btn btn-sm btn-block\'>Remove file</button>');
                    if(_this.options.maxFiles == 1){
                        that.removeDropzoneImage();
                        if(_this.files.length > 1){
                            _this.removeFile(_this.files[0]);
                        }
                    }
                    removeButton.addEventListener('click', function (e) {
                        e.preventDefault();
                        e.stopPropagation();
                        _this.removeFile(file);
                    });

                    file.previewElement.appendChild(removeButton);
                    if (file) {
                        let reader = new FileReader();
                        reader.onload = (e) => {
                            that.editCollectionForm.patchValue({avatar: reader.result});
                            this.isLogoLoaded = false;
                        };
                        reader.readAsDataURL(file);
                    }
                });

                this.on('removedfile', function (file) {
                    that.editCollectionForm.patchValue({avatar: null});
                });

                this.on('error', function (file, message: any) {
                    if (file) {
                        that.toastr.error(message);
                    }
                });
            }
        });
    }

    removeDropzoneImage() {
        this.isLogoLoaded = false;
        this.editCollectionForm.patchValue({avatar: null});
        this.editCollectionForm.get('avatar').value ? this['fileDropZone'].previewsContainer.lastChild['hidden'] = true : this['fileDropZone'].previewsContainer.lastChild['hidden']= false;
    }
}
