import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClient} from '@angular/common/http';
import {BsDatepickerModule, BsDropdownModule, CollapseModule, DatepickerModule, ModalModule, TabsModule, TooltipModule} from 'ngx-bootstrap';
import {ExportAsModule} from 'ngx-export-as';
import {NgxEditorModule} from 'ngx-editor';
import {NgxPermissionsModule} from 'ngx-permissions';
import {NgSelectModule} from '@ng-select/ng-select';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {DataTablesModule} from 'angular-datatables';

import {CollectionsRoutingModule} from './collections-routing.module';
import {SharedModule} from '../../../shared/shared.module';

import {CollectionListsComponent} from './pages/collection-lists/collection-lists.component';
import {CollectionCreateComponent} from './pages/collection-create/collection-create.component';
import {CollectionEditComponent} from './pages/collection-edit/collection-edit.component';
// import {CollectionViewComponent} from './pages/collection-view/collection-view.component';

@NgModule({
    declarations: [
        CollectionListsComponent,
        CollectionCreateComponent,
        CollectionEditComponent,
        // CollectionViewComponent
    ],
    imports: [
        CommonModule,
        CollectionsRoutingModule,
        FormsModule,
        ReactiveFormsModule,
        NgxPermissionsModule,
        DataTablesModule,
        ExportAsModule,
        NgxEditorModule,
        NgSelectModule,
        ModalModule.forRoot(),
        DatepickerModule.forRoot(),
        BsDatepickerModule.forRoot(),
        TooltipModule.forRoot(),
        BsDropdownModule.forRoot(),
        TranslateModule.forChild({
            loader: {
                provide: TranslateLoader,
                useFactory: (HttpLoaderFactory),
                deps: [HttpClient]
            }
        }),
        SharedModule,
        CollapseModule
    ],
})

export class CollectionsModule {
}

// Required for AOT compilation
export function HttpLoaderFactory(http: HttpClient) {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}
