import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {DatePipe} from '@angular/common';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
// import {CollectionSettingService} from '../../../../../core/services/collection-setting.service';
import {CollectionsService} from '../../../../../core/services/collections.service';
import {ItemService} from '../../../../../core/services/item.service';
import {ClientService} from '../../../../../core/services/client.service';
import {TaxService} from '../../../../../core/services/tax.service';
import {AuthenticationService} from '../../../../../core/services/authentication.service';
import {CategorysService} from '../../../../../core/services/categorys.service';
import { SeoService } from 'src/app/core/services/seo.service';
import {NgOption} from '@ng-select/ng-select';
import * as Dropzone from 'dropzone';

@Component({
    selector: 'app-collection-create',
    templateUrl: './collection-create.component.html',
    styleUrls: ['./collection-create.component.scss'],
    providers: [DatePipe]
})

export class CollectionCreateComponent implements OnInit {
    createCollectionForm: FormGroup;
    liststatus: NgOption[] = [
        {value: 'yes', label: 'Show'},
        {value: 'no', label: 'Hide'},
    ];
    listCondition: NgOption[] = [
        {value: 0, label: '>'},
        {value: 1, label: '<'},
        {value: 2, label: '='}
    ];
    listFilterType: NgOption[] = [
        {value: 0, label: 'Select'},
        {value: 1, label: 'Auto'},
    ];
    listFilterProductNeed: NgOption[] = [
        {value: 0, label: 'All Conditions'},
        {value: 1, label: 'Any condition'},
    ];

    listFilterCondition: NgOption[] = [
        {type: 0, condition: null, value: null, name: "Price"},
        {type: 1, condition: null, value: null, name: "Compare Price"},
        // {type: 2, condition: null, value: null, name: "Tag"}
    ];
    moduletype:string;
    urlPage:string;
    loginUser: any;
    listcollections = [];
    listcategorys = [];
    listTemplate: any = [];
    seoData: any;
    isFormSubmitted = false;
    isFormLoaded = false;
    datepickerConfig = {
        dateInputFormat: 'YYYY-MM-DD',
        containerClass: 'theme-red'
    };
    isCollapsedHead1 = false;
    isCollapsedHead2 = false;
    isCollapsedHead3 = false;
    @ViewChild('logodropzone', {static: false}) logodropzone: ElementRef;
    constructor(
        public translate: TranslateService,
        public datepipe: DatePipe,
        private router: Router,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private categorysService: CategorysService,
        private collectionsService: CollectionsService,
        private clientService: ClientService,
        private seoService: SeoService,
        private itemService: ItemService,
        private taxService: TaxService,
        // private collectionSettingService: CollectionSettingService,
        private authenticationService: AuthenticationService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
    }

    get collectionControl() {
        return this.createCollectionForm.controls;
    }

    ngOnInit() {
        if(this.router.url === '/productcollections/create'){
            this.moduletype = 'products';
            this.urlPage = '/productcollections';
        } else {
            this.moduletype = 'news';
            this.urlPage = '/newscollections';
        }
        this.getTemplate();
        this.getAllMeta();
        this.getCategorys();
    }

    loadForm() {
        let that = this;
        this.createCollectionForm = this.formBuilder.group({
            name: [null, Validators.required],
            description: [null],
            page_title: [null],
            page_description: [null],
            avatar: [null, Validators.required],
            url_and_handle: [null, Validators.required],
            active: [null, Validators.required],
            keyword: [null],
            condition_type: [null],
            type: [0],
            conditions: [],
            seoData: this.formBuilder.array([]),
            filter: this.formBuilder.array([]),
            seo: [null],
            po_types_indentify:[this.moduletype],
            pages_id: [null, Validators.required],
        });
        setTimeout(() => {
            that.loadDropzone();
        });


        // for (let filterCondition of this.listFilterCondition) {
        //     this.addFilterCondition(filterCondition);
        // }
        this.isFormLoaded = true;
    }
    getAllMeta() {
        this.seoService.getAllMeta()
            .subscribe(
                data => {
                    this.seoData = data;
                });
    }
    private getFilterCondition(data) {
        return this.formBuilder.group({
            type: [data.type, Validators.required],
            name: [data.name],
            condition: [null, Validators.required],
            value: [null, Validators.required]
        });
    }
    get controllFilter(): FormArray {
        return this.createCollectionForm.get('filter') as FormArray;
    } 
    private addFilterCondition(data) {
        const controlFilter = <FormArray>this.createCollectionForm.controls['filter'];
        controlFilter.push(this.getFilterCondition(data));
    }

    onAddFilterCondition(even: any) {
        const control = <FormArray>this.createCollectionForm.controls['filter'];
        control.push(this.getFilterCondition(even));
    }
    onRemoveFilterCondition(even: any) {
        for (var i = 0; i < this.createCollectionForm.value.filter.length; ++i) {
            if(even.value.type == this.createCollectionForm.value.filter[i].type){
                (<FormArray>this.createCollectionForm.get('filter')).removeAt(i)
            }
        }
    }

    private getSeoData(data) {
        return this.formBuilder.group({
            meta: [data.label, Validators.required],
            meta_id: [data.id, Validators.required],
            content: [null]
        });
    }
    private addSeoData(data) {
        const control = <FormArray>this.createCollectionForm.controls['seoData'];
        control.push(this.getSeoData(data));
    }
    onAddMetaTag(even: any) {
        const control = <FormArray>this.createCollectionForm.controls['seoData'];
        control.push(this.getSeoData(even));
    }
    onRemoveMetaTag(even: any) {
        for (var i = 0; i < this.createCollectionForm.value.seoData.length; ++i) {
            if(even.value.id == this.createCollectionForm.value.seoData[i].meta_id){
                (<FormArray>this.createCollectionForm.get('seoData')).removeAt(i)
            }
        }
    }
    getCategorys() {
        this.categorysService.getAll().subscribe(
            data => {
                this.listcategorys = data;
                this.loadForm();
            });
    }

    changeFilterType(data){
        console.log(data);
    }

    getTemplate() {
        this.collectionsService.getTemplate().subscribe(
            data => {
                this.listTemplate = data;
            });
    }


    onSubmit() {
        this.isFormSubmitted = true;
        if (this.createCollectionForm.invalid) {
            return;
        }
        this.collectionsService.create(this.createCollectionForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('collections.messages.create'), this.translate.instant('collections.title'));
                    if (this.moduletype === 'products') {
                        this.router.navigate(['productcollections']);
                    } else {
                        this.router.navigate(['newscollections']);
                    }
                });
    }

    loadDropzone() {
        let that = this;
        new Dropzone(this.logodropzone.nativeElement, {
            url: 'https://httpbin.org/post',
            maxFiles: 1,
            clickable: true,
            acceptedFiles: 'image/*',
            createImageThumbnails: true,
            init: function () {
                this.on('addedfile', function (file) {
                    const removeButton = Dropzone.createElement('<button class=\'btn btn-sm btn-block\'>' + that.translate.instant('common.remove_file') + '</button>');
                    const _this = this;
                    removeButton.addEventListener('click', function (e) {
                        e.preventDefault();
                        e.stopPropagation();
                        _this.removeFile(file);
                    });

                    file.previewElement.appendChild(removeButton);

                    if (file) {
                        let reader = new FileReader();
                        reader.onload = (e) => {
                            that.createCollectionForm.patchValue({avatar: reader.result});
                        };
                        reader.readAsDataURL(file);
                    }
                });

                this.on('removedfile', function (file) {
                    that.createCollectionForm.patchValue({avatar: null});
                });

                this.on('error', function (file, message: any) {
                    if (file) {
                        that.toastr.error(message);
                    }
                });
            }
        });
    }
}
