import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {NgxPermissionsGuard} from 'ngx-permissions';

import {CollectionListsComponent} from './pages/collection-lists/collection-lists.component';
import {CollectionCreateComponent} from './pages/collection-create/collection-create.component';
import {CollectionEditComponent} from './pages/collection-edit/collection-edit.component';
// import {CollectionViewComponent} from './pages/collection-view/collection-view.component';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: '',
            canActivate: [NgxPermissionsGuard],
            component: CollectionListsComponent
        },
        {
            path: 'create',
            canActivate: [NgxPermissionsGuard],
            data: {
                breadcrumbs: {
                    text: 'common.create',
                    icon: 'fa fa-external-link',
                    show: true,
                    isHome: true
                },
                permissions: {
                    only: ['admin', 'super_admin', 'collections_create']
                }
            },
            component: CollectionCreateComponent
        },
        {
            path: 'edit/:id',
            canActivate: [NgxPermissionsGuard],
            data: {
                breadcrumbs: {
                    text: 'common.edit',
                    icon: 'fa fa-external-link',
                    show: true,
                    isHome: true
                },
                permissions: {
                    only: ['admin', 'super_admin', 'collections_edit']
                }
            },
            component: CollectionEditComponent
        },
        // {
        //     path: 'detail/:id',
        //     canActivate: [NgxPermissionsGuard],
        //     data: {
        //         breadcrumbs: {
        //             text: 'common.detail',
        //             icon: 'fa fa-external-link'
        //         },
        //         permissions: {
        //             only: ['admin', 'super_admin', 'collections_view']
        //         }
        //     },
        //     component: CollectionViewComponent
        // }
    ]
}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class CollectionsRoutingModule {
}
